<template>
    <b-container>
        <b-row class="justify-content-center mt-4">
            <b-col md="12">
                <h1 class="mt-4" align="center" style="color: white">书单</h1>
            </b-col>
        </b-row>
        <b-row class="justify-content-center mt-4">
            <b-col md="auto">
                <b-card-group columns id="recommend-lists">
                    <b-card v-for="(list,index) in bookLists" :key="index"
                            bg-variant="dark" text-variant="white">
                        <template v-slot:header>
                            <h3 align="center">{{list.title}}</h3>
                        </template>
                        <b-card-text class="mt-2">
                            <div align="center" v-if="userInfo[list.uid]!=null">
                                {{userInfo[list.uid].name}}
                                <b-img width="30" height="30" alt="头像"
                                       v-bind:src="userInfo[list.uid].userface"
                                       rounded="circle"></b-img>
                            </div>
                            <p>{{list.text}}</p>
                        </b-card-text>
                        <b-list-group class="mt-2">
                            <b-list-group-item v-for="(item,index2) in list.items.slice(0,10)" :key="index2"
                                               variant="dark" :id="`book-list-${list.id}-item-${index2}`" button>
                                <div align="center">{{item.title}}</div>
                                <b-popover :target="`book-list-${list.id}-item-${index2}`" triggers="click"
                                           @show="getDetail(item)"
                                           @hidden="resetPopOver">
                                    <template v-slot:title>{{popoverItem.title}}</template>
                                    <b-img thumbnail
                                           v-bind:src="popImageUrl"
                                           center/>
                                    <br/>
                                    <ul>
                                        <li>评分: {{popoverItem.rating.average}}</li>
                                        <li>作者: {{popoverItem.author.join(',')}}</li>
                                        <li>isbn: {{popoverItem.isbn10+' | '+popoverItem.isbn13}}</li>
                                        <li>出版社: {{popoverItem.publisher}}</li>
                                        <li>简介: {{popoverItem.summary}}</li>
                                    </ul>
                                </b-popover>
                            </b-list-group-item>
                        </b-list-group>
                    </b-card>
                </b-card-group>
                <b-pagination
                        v-model="pagination.currentPage"
                        :total-rows="pagination.total"
                        :per-page="pagination.perPage"
                        aria-controls="recommend-lists"
                        align="center"
                ></b-pagination>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
    export default {
        name: "BookPopular",
        data() {
            return {
                bookLists: [],
                popoverItem: {
                    image: '',
                    author: '',
                    isbn10: '',
                    isbn13: '',
                    publisher: '',
                    rating: {
                        average: '',
                    },
                    attrs: {}
                },
                popImageUrl: '',
                pagination: {
                    prePage: 5,
                    currentPage: 1,
                    total: 0,
                },
                userInfo: {},
            }
        },
        mounted() {
            this.getMovieLists();
        },
        watch: {
            pagination: "getMovieList"
        },
        methods: {
            getMovieLists() {
                let url = "/recommendlist/basic/book/page/" + this.pagination.currentPage + '/' + this.pagination.prePage;
                this.getRequest(url).then(resp => {
                    if (resp) {
                        this.bookLists = resp;
                        this.userInfoUpdate(resp);
                        // Trick to reset/clear native browser form validation state
                        this.show = false;
                        this.$nextTick(() => {
                            this.show = true
                        })
                    }
                })
            },
            userInfoUpdate(lists) {
                for (let i = 0; i < lists.length; i++) {
                    this.getUserInfo(lists[i].uid);
                }
                console.log(this.userInfo);
            },
            getUserInfo(uid) {
                let url = "/user/info/" + uid;
                this.getRequest(url).then(resp => {
                    if (resp) {
                        // console.log(resp);
                        this.userInfo[uid] = resp;
                    }
                })
            },
            getDetail(item) {
                let id = item.id;
                let url = "/dapi/v2";
                switch (item.type) {
                    case "f":
                        url = url + "/movie/" + id;
                        break;
                    case "b":
                        url = url + "/book/" + id;
                        break;
                    case "m":
                        url = url + "/music/" + id;
                        break;
                }
                this.getRequest(url).then(resp => {
                    if (resp) {
                        this.popoverItem = resp;
                        this.popImageUrl = this.imageUrlCut(resp.image);
                    }
                })
            },
            imageUrlCut(url) {
                return url.replace(/https:\/\/img\d.doubanio.com/i, "/dpic");
            },
            resetPopOver() {
                this.popImageUrl = '';
                this.popoverItem = {
                    alt_title: '',
                    image: '',
                    summary: '',
                    rating: {
                        average: '',
                    },
                    attrs: {}
                };
            },
        }
    }
</script>

