<template>
    <b-container>
        <b-row class="justify-content-center mt-4">
            <h1 style="color: white">图书列表编辑</h1>
        </b-row>
        <b-row class="justify-content-center mt-4">
            <b-col md="4">
                <b-card bg-variant="dark" text-variant="light">
                    <template v-slot:header>
                        <h3 align="center">列表内容</h3>
                    </template>
                    <b-form-group label="标题" label-for="textarea-title">
                        <b-form-input
                                id="textarea-title"
                                v-model="recommendForm.title"
                                placeholder="请输入安利列表的标题"
                        ></b-form-input>
                    </b-form-group>
                    <b-form-group label="描述" label-for="textarea-text">
                        <b-form-textarea
                                id="textarea-text"
                                v-model="recommendForm.text"
                                placeholder="请输入安利列表的描述"
                                rows="5"
                        ></b-form-textarea>
                    </b-form-group>
                </b-card>
            </b-col>
            <b-col>
                <b-card bg-variant="dark" text-variant="light" align="center">
                    <template v-slot:header>
                        <h3 align="center">条目列表</h3>
                    </template>
                    <b-list-group>
                        <b-list-group-item button variant="dark" v-for="(item,index) in addList"
                                           :key="index" @click="deleteItem(addList, index)">
                            <div align="center">{{item.title}}</div>
                        </b-list-group-item>
                    </b-list-group>
                    <b-button variant="warning" @click="submitForm" class="mt-4">提交</b-button>
                    <b-card-footer>单击条目可移除</b-card-footer>
                </b-card>
            </b-col>
            <b-col md="5">
                <b-card bg-variant="dark" text-variant="light" align="center">
                    <template v-slot:header>
                        <h3 align="center">添加条目</h3>
                    </template>
                    <b-row class="mt-3">
                        <b-col>
                            <b-form-input v-model="searchText" placeholder="请输入需要搜索的内容，并在下面选择"/>
                        </b-col>
                        <b-col md="auto">
                            <b-button variant="success" @click="getDoubanSearch" class="ml-2">搜索</b-button>
                        </b-col>
                    </b-row>
                    <b-row class="mt-3">
                        <b-col>
                            <b-form-select v-model="selected" :options="options"></b-form-select>
                        </b-col>
                        <b-col md="auto">
                            <b-button @click="addToList" variant="info" class="ml-2">
                                添加
                            </b-button>
                        </b-col>
                    </b-row>
                </b-card>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
    export default {
        name: "BookEdit",
        data() {
            return {
                selected: null,
                options: [
                    {value: {}, text: '请在搜索框搜索图书条目'},
                ],
                addList: [],
                searchText: '',
                recommendForm: {
                    uid: 0,
                    title: '',
                    text: '',
                    items: '',
                    pubTime: ''
                },
            }
        },
        methods: {
            addToList() {
                if (this.addList.includes(this.selected) || this.selected.id == null) return;
                this.addList.push(this.selected);
            },
            getDoubanSearch() {
                let url = '/dbook/j/subject_suggest?q=' + encodeURI(this.searchText);
                this.getRequest(url).then(resp => {
                    if (resp) {
                        this.parseOptions(resp)
                    }
                })
            },
            parseOptions(rawSearchJson) {
                let Options = [];
                for (let i = 0; i < rawSearchJson.length; i++) {
                    if (rawSearchJson[i].type !== 'b') continue;
                    let option = {
                        value: {
                            id: rawSearchJson[i].id,
                            type: 'b',
                            title: rawSearchJson[i].title,
                        },
                        text: rawSearchJson[i].title + ' (' + rawSearchJson[i].year + ') ' + rawSearchJson[i].author_name
                    };
                    Options.push(option);
                }
                this.options = Options;
            },
            deleteItem(list, index) {
                list.splice(index, 1);
            },
            submitForm() {
                if (this.addList.length>0 && this.recommendForm.title.length>0){
                    this.recommendForm.items = this.addList;
                    this.recommendForm.uid = this.$store.state.currentUser.id;
                    this.recommendForm.pubTime = new Date();
                    this.postRequest("/recommendlist/basic/",this.recommendForm).then(resp => {
                        if (resp.status==200){
                            this.$router.push("/book/main")
                        }
                    })
                }
            }
        }
    }
</script>

